import React from "react";
import loogo from "../../Assets/welcome.svg";
import "./Welcome.css";

function WelcomePage({ handleViewChange }) {
  const handelCreataccountclick = () => {
    handleViewChange("signup");
  };
  const handelLoginClick = () => {
    handleViewChange("login");
  };
  return (
    <div className="Welcome-page">
      <h1>Hi, Welcome to</h1>
      <img src={loogo} alt="logo" />
      <div className="vendor-login-button">
        <button className="welcome-login-button" onClick={handelLoginClick}>
          Login
        </button>
      </div>
      <a href="#" className="create-account" onClick={handelCreataccountclick}>
        Create New Account
      </a>
    </div>
  );
}

export default WelcomePage;
