import React, { useState } from "react";
import background from "../../Assets/login.svg";
import "./VendorLandingPage.css";
// import VendorLogin from "./VendorLogin";
import WelcomePage from "../WelcomePage/WelcomePage";
import VendorLoginform from "../Login/VendorLoginform";
import Signup from "../Signup/VendorSignup";
function NewVendorlLogin() {
  const [currentView, setCurrentView] = useState("WelcomePage");
  const handleViewChange = (view) => {
    setCurrentView(view);
  };
  return (
    <div>
      <div className="background-img-vendor">
        <img src={background} alt="Left Design" />
      </div>
      <div className="content-login-signup-welcome">
        {currentView === "WelcomePage" && (
          <WelcomePage handleViewChange={handleViewChange} />
        )}
        {currentView === "signup" && (
          <Signup handleViewChange={handleViewChange} />
        )}
        {currentView === "login" && (
          <VendorLoginform handleViewChange={handleViewChange} />
        )}
      </div>
    </div>
  );
}

export default NewVendorlLogin;
