const currency = "";

function OrderList({ orders, refresh }) {
  const [confirmModal, setConfirmModal] = useState(false);
  const [selectedOrder, setSelectedOrder] = useState(null);
  const [rejectModal, setRejectModal] = useState(false);
  const [dineinModal, setDineinModal] = useState(false);

  const handleConfirmModalClose = () => {
    setConfirmModal(false);
  };

  const handleRejectModalClose = () => {
    setRejectModal(false);
  };

  const handleDineinModalClose = () => {
    setDineinModal(false);
  };

  const handleAccept = (order) => {
    if (order.orderType === "Takeaway") {
      setConfirmModal(true);
      setSelectedOrder(order);
    } else {
      setDineinModal(true);
      setSelectedOrder(order);
    }
  };

  const handleReject = (order) => {
    setRejectModal(true);
    setSelectedOrder(order);
  };

  const [expandedOrderIndex, setExpandedOrderIndex] = useState(null);
  const handleCopy = (orderId) => {
    navigator.clipboard.writeText(orderId.toString());
    alert(`Order ID #${orderId} copied to clipboard!`);
  };

  const toggleExpand = (index) => {
    if (expandedOrderIndex === index) {
      setExpandedOrderIndex(null);
    } else {
      setExpandedOrderIndex(index);
    }
  };
  return (
    <div className="order-list">
      {orders.map((order, index) => (
        <div className="order-card" key={order.id}>
          <div className="orderIdDetails">
            <div className="orderId">
              <p>Order ID #{order.id}</p>
            </div>
            <div className="copyButton" onClick={() => handleCopy(order.id)}>
              <img src={copy} alt="copy" />
            </div>
          </div>
          <div className="OrderName">
            <p>{order.user.name}</p>
          </div>
          <div className="ordertimesdetails">
            <div className="time">{convertToIST(order.updatedtime).time}</div>
            <div className="seperation">|</div>
            <div className="orderdayDate">
              {convertToIST(order.updatedtime).date}
            </div>
          </div>
          <div className="orderditemListaction">
            <div
              className="order-summary-viewOrder"
              onClick={() => toggleExpand(index)}
            >
              {expandedOrderIndex === index ? (
                <div className="Expandable-items-vieworder">
                  <div className="items-list-vieworder">
                    {order.user.items[0].quantity} X {order.user.items[0].name}
                  </div>
                  <span className="expand-icon"></span>
                </div>
              ) : (
                <div className="nonExpandable">
                  {order.user.items[0].quantity} X {order.user.items[0].name}
                  <span className="expand-icon"></span>
                </div>
              )}
              {expandedOrderIndex === index && (
                <div className="order-details-vieworder">
                  {/* {order.items} */}
                  {order.user.items.slice(1).map((item, index) => (
                    <div key={index} className="order-item-detail">
                      <p>
                        {item.quantity} x {item.name}
                      </p>
                    </div>
                  ))}
                  <p>
                    Subtotal: {currency} {order.user.subTotal}
                  </p>
                  <p>Pickup Time: {order.user.pickupTime}</p>
                  <p>Order Status: {order.user.orderStatus}</p>
                </div>
              )}
            </div>
            <div className="actionbuttonsitems">
              <button className="reject" onClick={() => handleReject(order)}>
                Reject
              </button>
              <button className="accept" onClick={() => handleAccept(order)}>
                Accept
              </button>
            </div>
          </div>
        </div>
      ))}
      <ConfirmationModal
        isOpen={confirmModal}
        onClose={handleConfirmModalClose}
        order={selectedOrder}
        onRejectClicked={() => {
          setConfirmModal(false);
          setRejectModal(true);
        }}
        refresh={refresh}
      />
      <ConfirmationDinein
        isOpen={dineinModal}
        order={selectedOrder}
        onClose={handleDineinModalClose}
        onRejectClicked={() => {
          setDineinModal(false);
          setRejectModal(true);
        }}
        refresh={refresh}
      />
      <RejectModal
        isOpen={rejectModal}
        onClose={handleRejectModalClose}
        order={selectedOrder}
        refresh={refresh}
      />
    </div>
  );
}

export default OrderList;
