// Status.js
import React, { useEffect, useState } from "react";
import "./Status.css";
import "./Toggle.css";
import totalOrder from "../../../Assets/totalOrders.svg";
import pendingOrder from "../../../Assets/pending.svg";
import canceledOrder from "../../../Assets/canceled.svg";
import revenueOrder from "../../../Assets/Revenue.svg";
import api from "../../../services/api";
// stored the status in the state and toggled the status through redux
const Status = ({ name }) => {
  const [isOpen, setIsOpen] = useState(true);
  useEffect(() => {
    api
      .get(`/resturantStatus/${name}`)
      .then((response) => {
        console.log(response.data.forceClose);
        setIsOpen(!response.data.forceClose);
      })
      .catch((error) => {
        console.log(error);
      });
  }, [name]);

  const handleToggle = () => {
    api
      .patch(`/restaurant/update/${name}`)
      .then((response) => {
        setIsOpen(!isOpen);
      })
      .catch((error) => {
        console.log(error);
      });
  };

  return (
    <section className="status">
      <div className="toggle-status">
        <div className="statusContent">
          <h3>Restaurant Status:</h3>
        </div>
        <div className="statusAction">
          <span className={`statusActionText ${isOpen ? "statusActive" : ""}`}>
            Closed
          </span>
          <label className="switch">
            <input type="checkbox" checked={isOpen} onChange={handleToggle} />
            <span className="slider round"></span>
          </label>
          <span className={`statusActionText ${!isOpen ? "statusActive" : ""}`}>
            Open
          </span>
        </div>
      </div>
      <div className="statusCards">
        <div className="statusCard">
          <div>
            <img src={totalOrder} alt="totalorder" />
          </div>
          <div>
            <div className="StatusHeading">Total Orders</div>
            <div className="StatusValues">1750</div>
          </div>
          <div className="StatusGrowth">8.5</div>
        </div>
        <div className="statusCard">
          <div>
            <img src={pendingOrder} alt="pendingOrder" />
          </div>
          <div className="StatusContent">
            <div className="StatusHeading">Pending Orders</div>
            <div className="StatusValues">567</div>
          </div>
        </div>
        <div className="statusCard">
          <div>
            <img src={revenueOrder} alt="revenue" />
          </div>
          <div className="StatusContent">
            <div className="StatusHeading">Total Revenue</div>
            <div className="StatusValues">1,29,750</div>
          </div>
        </div>
        <div className="statusCard">
          <div>
            <img src={canceledOrder} alt="cancel" />
          </div>
          <div className="StatusContent">
            <div className="StatusHeading">Total Canceled</div>
            <div className="StatusValues">125</div>
          </div>
        </div>
      </div>
    </section>
  );
};

export default Status;
