import React, { useState } from "react";
import Sidebar from "../sidebar/SidebarDashboard";

import "./DashboardIntegration.css";
import DashboardContents from "../dashboard/DashboardContents";
import Menu from "../Menu/Menu";
import OrderPageIntegration from "../Order/OrderPageIntegration";
// import Menu from "../../Menu/Menu";
function DashboardIntegrate() {
  const [selectPage, setSelectPage] = useState("Dashboard");
  const handleSelectPage = (page) => {
    setSelectPage(page);
  };
  return (
    <div className="Dashboard">
      <div className="SidebarDashboard">
        <Sidebar activePage={selectPage} onSelectChange={handleSelectPage} />
      </div>
      {selectPage === "Dashboard" ? (
        <DashboardContents />
      ) : selectPage === "Menu" ? (
        <Menu />
      ) : selectPage === "Orders" ? (
        <OrderPageIntegration />
      ) : (
        <div>Page Not Found</div>
      )}{" "}
    </div>
  );
}

export default DashboardIntegrate;
