import React, { useState, useEffect } from "react";
import MenuHeader from "./menuHeader/MenuHeader";
import "./Menu.css";
import CategoryFilter from "./categoriesMenu/categoryFilter";
import MenuTable from "./menuList/menu";
import api from "../../services/api";
import { useNavigate } from "react-router-dom";
import OrderModal from "./addMore/orderModal";
const categories = [
  "All",
  "Appetizers",
  "Main Courses",
  "Desserts",
  "Beverages",
  "Pasta & Noodles",
  "Pizzas",
];

function Menu() {
  const [activeCategory, setActiveCategory] = useState("All");
  const [itemlist, setItemList] = useState([]);
  const [refresh, setRefresh] = useState(false);
  const [isAddDishFormOpen, setIsAddDishFormOpen] = useState(false);
  const [searchQuery, setSearchQuery] = useState("");
  const navigate = useNavigate();

  const handleCategorySelect = (category) => {
    setActiveCategory(category);
  };

  const handleSearchChange = (event) => {
    setSearchQuery(event.target.value);
  };

  async function fetchItem() {
    const token = localStorage.getItem("token");
    if (!token) {
      navigate("/", { replace: true });
    }
    const TokenString = JSON.parse(token);
    const name = TokenString.restaurant;
    await api
      .get(`/menu/restaurants/${name}`)
      .then((response) => {
        setItemList(response.data.menu);
        setRefresh(false);
      })
      .catch((error) => {
        console.log(error);
      });
  }

  const handleRefresh = () => {
    setRefresh(true);
  };

  const filterdDishList = itemlist.filter((item) => {
    if (activeCategory === "All") return true;
    return item.dishId.dishCatagory === activeCategory;
  });

  const reorderDishlist = () => {
    if (!searchQuery) return filterdDishList;
    const search = searchQuery.toLowerCase();
    const matchinDish = filterdDishList.filter((item) => {
      return item.dishId.dishName.toLowerCase().includes(search);
    });
    const nonMatchingDish = filterdDishList.filter((item) => {
      return !item.dishId.dishName.toLowerCase().includes(search);
    });
    return [...matchinDish, ...nonMatchingDish];
  };

  useEffect(() => {
    fetchItem();
  }, [refresh]);

  const handleAddDishForm = () => {
    setIsAddDishFormOpen(true);
  };

  const handleCloseAddDishForm = () => {
    setIsAddDishFormOpen(false);
  };
  return (
    <div className="MenuContent">
      <MenuHeader
        searchQuery={searchQuery}
        handleSearchChange={handleSearchChange}
      />
      <h3
        style={{
          textAlign: "left",
          paddingLeft: "20px",
          marginTop: "30px",
          marginBottom: "30px",
        }}
      >
        Category
      </h3>
      <div className="category">
        <CategoryFilter
          categories={categories}
          activeCategory={activeCategory}
          onSelectCategory={handleCategorySelect}
        />

        <div className="addDish" onClick={handleAddDishForm}>
          <button>+Add More</button>
        </div>
      </div>
      <div className="menulist-table">
        <MenuTable items={reorderDishlist()} refresh={handleRefresh} />
      </div>
      <OrderModal
        isOpen={isAddDishFormOpen}
        onClose={handleCloseAddDishForm}
        refresh={handleRefresh}
      />
    </div>
  );
}

export default Menu;
