import React, { useEffect, useState } from "react";
import "../OrderTable.css";
import convertToIST from "../../../../services/istTime";
function OrdersTableInprogressDinein({ items }) {
  const [showData, setShowData] = useState([]);

  useEffect(() => {
    setShowData(
      items.filter((order) => {
        return (
          order.user.orderStatus !== "Cancelled" &&
          order.user.orderStatus === "Accepted"
        );
      })
    );
  }, [items]);
  return (
    <table className="inProgressTable">
      <thead>
        <tr>
          <th>Order ID</th>
          <th>Name</th>
          <th>Order Time</th>
          <th>Arival Time</th>
          <th>Status</th>
          <th>Items</th>
          <th>Total Amt.</th>
          <th>Payment</th>
          <th>Actions</th>
        </tr>
      </thead>
      <tbody>
        {showData.length === 0 ? (
          <tr>
            <td colSpan="9">No Request Available</td>
          </tr>
        ) : (
          showData.map((item) => (
            <tr key={item.id}>
              <td>
                <p>{item.id}</p>
              </td>
              <td>{item.user.name}</td>
              <td>{convertToIST(item.createdTime).time}</td>
              <td>{item.user.pickupTime}</td>
              <td>
                <div className="preparing">preparing</div>
              </td>
              <td>{item.user.items.length}</td>
              <td> {item.user.subTotal}</td>
              <td>Paid</td>
              <td>
                <button
                  className="mark-unavailable"
                  // onClick={() => handleMarkUnavaliable(item._id)}
                >
                  Update
                </button>

                <button
                  className="delete"
                  // onClick={() => handleDeleteItem(item._id)}
                >
                  Cancel
                </button>
                <button className="more-options"></button>
              </td>
            </tr>
          ))
        )}
      </tbody>
    </table>
  );
}

export default OrdersTableInprogressDinein;
