import React, { useState, useRef, useEffect } from "react";
import "../Menu/addMore/orderModal.css";
import "./modalComponent.css";
import copy from "../../Assets/copy.svg";
import convertToIST from "../../services/istTime";
import api from "../../services/api";

function RejectModal({ isOpen, onClose, refresh, order }) {
  const currency = "";
  const modalRef = useRef(null);
  const [selectedReason, setSelectedReason] = useState("");

  const handleReasonChange = (event) => {
    setSelectedReason(event.target.value); // Set selected reason
  };

  const handleCopy = (orderId) => {
    navigator.clipboard.writeText(orderId.toString());
    alert(`Order ID #${orderId} copied to clipboard!`);
  };

  const handleOnClose = () => {
    setSelectedReason("");
    onClose();
  };

  const handleReject = () => {
    if (!selectedReason) {
      alert("Please select a reason to reject the order!");
      return;
    }
    api
      .delete(`/orders/Rejected/${order.id}`, {
        headers: { reason: selectedReason },
      })
      .then(() => {
        refresh();
        handleOnClose();
      })
      .catch((error) => {
        console.log(error);
      });
  };
  if (!isOpen) return null;
  return (
    <div className="modal-overlay-addDishes">
      <div className="modal-content-addDishes" ref={modalRef}>
        <div className="close" onClick={handleOnClose}>
          <span></span>
        </div>
        <div className="modal-confirm-header">
          <h6>REJECT ORDER?</h6>
        </div>
        <div className="content-details-dishes">
          <div className="orderIdDetails">
            <div className="orderId">
              <p>Order ID #{order.id}</p>
            </div>
            <div className="copyButton" onClick={() => handleCopy(order.id)}>
              <img src={copy} alt="copy" />
            </div>
          </div>
          <div className="customeDetails-orderType">
            <div className="CustomerDetails">
              <div className="OrderName">
                <p>{order.user.name}</p>
              </div>
              <div className="ordertimesdetails">
                <div className="time">
                  {convertToIST(order.createdTime).time}
                </div>
                <div className="seperation">|</div>
                <div className="orderdayDate">
                  {convertToIST(order.createdTime).date}
                </div>
              </div>
            </div>
            <div className="orderType">
              <p>{order.orderType.replace("Dine-in", "Dine In")}</p>
            </div>
          </div>
          <div className="itemDetails">
            {order.user.items.map((item, index) => (
              <div className="item-unavaliable" key={index}>
                <div className="Single-Items">
                  <p>{item.name}</p>
                  <small>
                    {currency}
                    {item.price} | Qty: {item.quantity}
                  </small>
                </div>
                <div className="checkbox-markunavaliable">
                  <input type="checkbox" />
                  <label htmlFor="unavailable">Mark Unavailable</label>
                </div>
              </div>
            ))}

            <div className="hline"></div>
            <div className="Reasons">
              <h4>Choose reason to Cancel</h4>
              <div>
                <input
                  type="radio"
                  id="reason1"
                  name="cancelReason"
                  value="Reason 1"
                  checked={selectedReason === "Reason 1"}
                  onChange={handleReasonChange}
                />
                <label htmlFor="reason1">Reason 1</label>
              </div>
              <div>
                <input
                  type="radio"
                  id="reason2"
                  name="cancelReason"
                  value="Reason 2"
                  checked={selectedReason === "Reason 2"}
                  onChange={handleReasonChange}
                />
                <label htmlFor="reason2">Reason 2</label>
              </div>
            </div>
            <div className="Action-button">
              <button className="reject" onClick={handleReject}>
                Reject
              </button>
              <button className="accept">Call Customer</button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default RejectModal;
