// MenuTable.js
import React, { useState } from "react";
import "./MenuTable.css";
import api from "../../../services/api";
const MenuTable = ({ items, refresh }) => {
  const [selectedItems, setSelectedItems] = useState([]);
  const handleSelectAll = (event) => {
    if (event.target.checked) {
      setSelectedItems(items.map((item) => item._id));
    } else {
      setSelectedItems([]);
    }
  };

  const handleSelectItem = (id) => {
    setSelectedItems((prevSelectedItems) =>
      prevSelectedItems.includes(id)
        ? prevSelectedItems.filter((itemId) => itemId !== id)
        : [...prevSelectedItems, id]
    );
  };

  const handleMarkUnavaliable = (id) => {
    api
      .patch(`/menu/toggleAvailability/${id}`)
      .then((response) => {
        refresh();
      })
      .catch((error) => {
        console.log(error);
      });
  };

  const handleDeleteItem = (id) => {
    api
      .delete(`/menu/deleteDish/${id}`)
      .then((response) => {
        refresh();
      })
      .catch((error) => {
        console.log(error);
      });
  };

  const handleToggleAvailability = () => {
    api
      .patch("/updateToggleAvalability", { selectedItems })
      .then((response) => {
        setSelectedItems([]);
        refresh();
      })
      .catch((error) => {
        console.log(error);
      });
  };

  return (
    <table className="menu-table">
      <thead>
        <tr>
          <th>
            <input
              type="checkbox"
              onChange={handleSelectAll}
              checked={selectedItems.length === items.length}
            />
          </th>
          <th>Name</th>
          <th>Price</th>
          <th>Category</th>
          <th>
            Actions
            {selectedItems.length > 0 && (
              <button
                className="mark-unavailable mark-all"
                onClick={handleToggleAvailability}
              >
                Mark Available
              </button>
            )}
          </th>
        </tr>
      </thead>
      {items.length === 0 ? (
        <tbody>
          <tr>
            <td colSpan="5">No items found</td>
          </tr>
        </tbody>
      ) : (
        <tbody>
          {items.map((item, index) => (
            <tr
              key={item._id}
              className={item.available ? "avalaibleDish" : `unavalaibleDish`}
            >
              <td>
                <input
                  type="checkbox"
                  checked={selectedItems.includes(item._id)}
                  onChange={() => handleSelectItem(item._id)}
                />
              </td>
              <td>{item.dishId.dishName}</td>
              <td> {item.resturantDishPrice}</td>
              <td>{item.dishId.dishCatagory}</td>
              <td>
                {item.available ? (
                  <button
                    className="mark-unavailable"
                    onClick={() => handleMarkUnavaliable(item._id)}
                  >
                    Mark Unavailable
                  </button>
                ) : (
                  <button
                    className="mark-unavailable"
                    onClick={() => handleMarkUnavaliable(item._id)}
                  >
                    Mark Available
                  </button>
                )}
                <button
                  className="delete"
                  onClick={() => handleDeleteItem(item._id)}
                >
                  Delete
                </button>
                <button className="more-options"></button>
              </td>
            </tr>
          ))}
        </tbody>
      )}
    </table>
  );
};

export default MenuTable;
