import React, { useState, useEffect } from "react";
import "./VendorLoginPage.css";
import logo from "../../Assets/logocredentials.svg";
import api from "../../services/api";
import { useNavigate } from "react-router-dom";
import eye from "../../Assets/EyeFill.svg";
import passwordHide from "../../Assets/passwordHide.svg";

function VendorLoginform({ handleViewChange }) {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [rememberMe, setRememberMe] = useState(false);
  const [showPassword, setShowPassword] = useState(false);
  const navigate = useNavigate();

  const handleLogin = (e) => {
    e.preventDefault();
    // Implement login logic here
    api
      .post("/login", { email, password })
      .then((response) => {
        localStorage.setItem("token", JSON.stringify(response.data.token));
        navigate("/V2");
      })
      .catch((error) => {
        console.log(error);
      });
  };
  useEffect(() => {
    const token = localStorage.getItem("token");
    if (token) {
      localStorage.removeItem("token");
    }
  }, []);

  const handleSignupClicked = () => {
    handleViewChange("signup");
  };
  return (
    <div>
      <div className="login-container">
        <div className="login-box">
          <img src={logo} alt="Logo" className="logo" />
          <h1>Hi, Welcome</h1>
          <p className="welcome-text">Please login to continue</p>
          <form onSubmit={handleLogin}>
            <div className="input-group">
              <label>Email</label>
              <input
                type="text"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
                placeholder="sophat.leat@nintrea.live"
                required
              />
            </div>
            <div className="input-group">
              <label>Password</label>
              <div className="password-container">
                <input
                  type={showPassword ? "text" : "password"}
                  value={password}
                  onChange={(e) => setPassword(e.target.value)}
                  placeholder="Please Enter Your Password"
                  required
                />
                <span
                  className="toggle-password"
                  onClick={() => setShowPassword(!showPassword)}
                >
                  {showPassword ? (
                    <img src={passwordHide} alt="hide" />
                  ) : (
                    <img src={eye} alt="show" />
                  )}{" "}
                </span>
              </div>
            </div>
            <div className="options">
              <label className="remember-me">
                <input
                  type="checkbox"
                  checked={rememberMe}
                  onChange={(e) => setRememberMe(e.target.checked)}
                />
                Remember Me
              </label>
              <a href="/" className="forgot-password">
                Forgot Password
              </a>
            </div>
            <button type="submit" className="login-button">
              Login
            </button>
          </form>
          <p className="sign-up">
            Don't have an account?{" "}
            <a href="#" onClick={handleSignupClicked}>
              Sign Up
            </a>
          </p>
        </div>
      </div>
    </div>
  );
}

export default VendorLoginform;
