import React, { useState } from "react";
import "./signupVendor.css";
import logo from "../../Assets/logocredentials.svg";
import eye from "../../Assets/EyeFill.svg";
import passwordHide from "../../Assets/passwordHide.svg";

const Signup = ({ handleViewChange }) => {
  const [email, setEmail] = useState("");
  const [phone, setPhone] = useState("");
  const [password, setPassword] = useState("");
  const [confirmPassword, setConfirmPassword] = useState("");
  const [showPassword, setShowPassword] = useState(false);

  const handleSignup = (e) => {
    e.preventDefault();
    // Implement signup logic here
    console.log("Email:", email);
    console.log("Phone:", phone);
    console.log("Password:", password);
    console.log("Confirm Password:", confirmPassword);
  };

  const handleLoginClicked = () => {
    handleViewChange("login");
  };
  return (
    <div className="signup-container">
      <div className="signup-box">
        <img src={logo} alt="Logo" className="logo" />
        <h1>Create an account</h1>
        <p className="welcome-text">Signup to Continue</p>
        <form onSubmit={handleSignup}>
          <div className="input-group">
            <label>Email Address</label>
            <input
              type="email"
              value={email}
              onChange={(e) => setEmail(e.target.value)}
              placeholder="Enter your email"
              required
            />
          </div>
          <div className="input-group">
            <label>Phone Number</label>
            <input
              type="tel"
              value={phone}
              onChange={(e) => setPhone(e.target.value)}
              placeholder="+855 Enter your phone number"
              required
            />
          </div>
          <div className="input-group">
            <label>Password</label>
            <div className="password-container">
              <input
                type={showPassword ? "text" : "password"}
                value={password}
                onChange={(e) => setPassword(e.target.value)}
                placeholder="Please Enter Your Password"
                required
              />
              <span
                className="toggle-password"
                onClick={() => setShowPassword(!showPassword)}
              >
                {showPassword ? "Hide" : "Show"}
              </span>
            </div>
          </div>
          <div className="input-group">
            <label>Confirm Password</label>
            <div className="password-container">
              <input
                type={showPassword ? "text" : "password"}
                value={password}
                onChange={(e) => setConfirmPassword(e.target.value)}
                placeholder="Please Enter Your Password"
                required
              />
              <span
                className="toggle-password"
                onClick={() => setShowPassword(!showPassword)}
              >
                {showPassword ? (
                  <img src={passwordHide} alt="hide" />
                ) : (
                  <img src={eye} alt="show" />
                )}
              </span>
            </div>
          </div>
          <button type="submit" className="signup-button">
            Sign Up
          </button>
        </form>
        <p className="login-link">
          Already have an account?{" "}
          <a href="#" onClick={handleLoginClicked}>
            Login
          </a>
        </p>
      </div>
    </div>
  );
};

export default Signup;
