// Sidebar.js
import React from "react";
import "./Sidebar.css";
import Logo from "../../Assets/logo.svg";
import home from "../../Assets/home.svg";
import menu from "../../Assets/menu.svg";
import orders from "../../Assets/order.svg";
import settings from "../../Assets/settings.svg";

const Sidebar = ({ activePage, onSelectChange }) => (
  <div className="sidebar">
    <div className="sidebarlogo">
      <img src={Logo} alt="logo" />
    </div>
    <ul style={{ color: "#B1B1B1", fontSize: "20px" }}>
      <li
        className={activePage === "Dashboard" ? `active` : ""}
        onClick={() => onSelectChange("Dashboard")}
      >
        <div className="sidebar-li">
          <img
            src={home}
            alt="home"
            className={activePage === "Dashboard" ? `selectedImg` : ""}
          />
          Dashboard
        </div>
      </li>
      <li
        className={activePage === "Menu" ? `active` : ""}
        onClick={() => onSelectChange("Menu")}
      >
        <div className="sidebar-li">
          <img
            src={menu}
            alt="menu"
            className={activePage === "Menu" ? `selectedImg` : ""}
          />
          Menu
        </div>
      </li>
      <li
        className={activePage === "Orders" ? `active` : ""}
        onClick={() => onSelectChange("Orders")}
      >
        <div className="sidebar-li">
          <img
            src={orders}
            alt="order"
            className={activePage === "Orders" ? `selectedImg` : ""}
          />
          Orders
        </div>
      </li>
      <li>
        <div className="sidebar-li">
          <img src={settings} alt="settings" />
          Settings
        </div>
      </li>
    </ul>
  </div>
);

export default Sidebar;
