// Orders.js
import React, { useState } from "react";
import "./Orders.css";
import OrderList from "./OrderList";
import InpogressOrderList from "./InpogressOrderList";

const Orders = ({ order, refresh }) => {
  const [activeTab, setActiveTab] = useState("Takeaway");
  const [inprogressactiveTab, setInprogressActiveTab] = useState("Takeaway");
  const filteredOrders = order.filter(
    (ordereach) =>
      ordereach.orderType === activeTab &&
      ordereach.user.orderStatus !== "Accepted" &&
      ordereach.user.orderStatus !== "Cancelled"
  );
  const filteredInprogressOrders = order.filter(
    (ordereach) =>
      ordereach.orderType === inprogressactiveTab &&
      ordereach.user.orderStatus === "Accepted"
  );

  return (
    <>
      <div className="orderListInprogress">
        <section className="orders">
          <div className="order-tabs">
            <button
              className={activeTab === "Takeaway" ? "active" : ""}
              onClick={() => setActiveTab("Takeaway")}
            >
              Take Away
            </button>
            <button
              className={activeTab === "Dine-in" ? "active" : ""}
              onClick={() => setActiveTab("Dine-in")}
            >
              Dine In
            </button>
          </div>
          <div className="horizontlLine"></div>
          {filteredOrders.length === 0 && (
            <div className="no-orders">No orders available</div>
          )}
          <OrderList orders={filteredOrders} refresh={refresh} />
        </section>
        <section className="inProgress">
          <div className="order-tabs">
            <button
              className={inprogressactiveTab === "Takeaway" ? "active" : ""}
              onClick={() => setInprogressActiveTab("Takeaway")}
            >
              Take Away
            </button>
            <button
              className={inprogressactiveTab === "Dine-in" ? "active" : ""}
              onClick={() => setInprogressActiveTab("Dine-in")}
            >
              Dine In
            </button>
          </div>
          <div className="horizontlLine"></div>
          {filteredInprogressOrders.length === 0 && (
            <div className="no-orders">No Pending orders available</div>
          )}
          <InpogressOrderList orders={filteredInprogressOrders} />
        </section>
      </div>
    </>
  );
};

export default Orders;
