import React, { useEffect, useState } from "react";
import OrdersTableTakeAway from "./OrdersTable/Requests/OrdersTableTakeAway";
import OrdersTableInprogressTakeaway from "./OrdersTable/InPreogress/OrdersTableInprogressTakeaway";
import OrdersTableDinein from "./OrdersTable/Requests/OrderTableDinein";
import OrdersTableInprogressDinein from "./OrdersTable/InPreogress/OrderTableInProgressDinein";

function TableIntegration({ selectTitle, orderType, orderData, refresh }) {
  const [orderTakeAwayData, setOrderTakeAwayData] = useState([]);
  const [orderDineInData, setOrderDineInData] = useState([]);

  const [orderCompletedData, setOrderCompletedData] = useState([]);
  useEffect(() => {
    setOrderTakeAwayData(
      orderData.filter((order) => order.orderType === "Takeaway")
    );
    setOrderDineInData(
      orderData.filter((order) => order.orderType === "Dine-in")
    );
  }, [orderData]);
  return (
    <div className="tablesOrder">
      {orderType === "Take Away" ? (
        selectTitle === "Request" ? (
          <OrdersTableTakeAway items={orderTakeAwayData} refresh={refresh} />
        ) : (
          <OrdersTableInprogressTakeaway
            items={orderTakeAwayData}
            refresh={refresh}
          />
        )
      ) : orderType === "Dine in" ? (
        selectTitle === "Request" ? (
          <OrdersTableDinein items={orderDineInData} refresh={refresh} />
        ) : (
          <OrdersTableInprogressDinein
            items={orderDineInData}
            refresh={refresh}
          />
        )
      ) : orderType === "Completed" ? (
        <h1>Completed</h1>
      ) : (
        // <OrdersTableCompleted />
        <h1>Cancelled</h1>
        // <OrdersTableCancelled />
      )}
    </div>
  );
}

export default TableIntegration;
