import React, { useState, useEffect } from "react";
import OrderHeaders from "./orderHeader/OrderHeaders";
import "./OrderPageIntegration.css";
import CatagoriesOrder from "./CatagoriesOrder/CatagoriesOrder";
import OrdersTableTakeAway from "./OrdersTable/Requests/OrdersTableTakeAway";
import OrdersTableInprogressTakeaway from "./OrdersTable/InPreogress/OrdersTableInprogressTakeaway";
import TableIntegration from "./TableIntegration";
import { useNavigate } from "react-router-dom";
import api from "../../services/api";

function OrderPageIntegration() {
  const [activeCategory, setActiveCategory] = useState("Take Away");
  const [searchQuery, setSearchQuery] = useState("");
  const [refresh, setRefresh] = useState(false);
  const [orders, setOrders] = useState([]);
  const [resturantName, setResturantName] = useState("");

  const [selectTitle, setSelectTitle] = useState("Request");
  const navigate = useNavigate();
  useEffect(() => {
    const token = localStorage.getItem("token");
    if (!token) {
      navigate("/", { replace: true });
    }
    const TokenString = JSON.parse(token);
    const name = TokenString.restaurant;
    setResturantName(name);

    api
      .get(`/orders/takeaway/${name}`)
      .then((response) => {
        setOrders(response.data.orders);
      })
      .catch((error) => {
        console.log(error);
      });
    setRefresh(false);
  }, [refresh]);

  const handleRefresh = () => {
    setRefresh(true);
  };

  const handleSearchChange = (event) => {
    setSearchQuery(event.target.value);
  };

  const getReorderedOrders = () => {
    if (!searchQuery) {
      return orders; // Return all orders if no search query
    }

    // Separate matching and non-matching orders
    const matchingOrders = orders.filter((order) =>
      order.id.toLowerCase().includes(searchQuery.toLowerCase())
    );

    const nonMatchingOrders = orders.filter(
      (order) => !order.id.toLowerCase().includes(searchQuery.toLowerCase())
    );

    // Combine matching orders at the top
    return [...matchingOrders, ...nonMatchingOrders];
  };

  const onSelectCategory = (category) => {
    setActiveCategory(category);
  };
  return (
    <div className="OrdersContent">
      <OrderHeaders />
      <div className="catagoreisOrders">
        <CatagoriesOrder
          activeCategory={activeCategory}
          onSelectCategory={onSelectCategory}
        />
      </div>
      <div className="RequestInprogress">
        <h5
          className={`SelectTitleProgress ${
            selectTitle === "Request" ? "active" : ""
          }`}
          onClick={() => setSelectTitle("Request")}
        >
          Requests
        </h5>
        <h5
          className={`SelectTitleProgress ${
            selectTitle === "In Progress" ? "active" : ""
          }`}
          onClick={() => setSelectTitle("In Progress")}
        >
          In Progress
        </h5>
      </div>
      <div className="horLine"></div>
      <TableIntegration
        selectTitle={selectTitle}
        orderType={activeCategory}
        orderData={orders}
        refresh={handleRefresh}
      />
    </div>
  );
}

export default OrderPageIntegration;
