import React, { useState, useRef, useEffect } from "react";
import "./modalComponent.css";
import "../Menu/addMore/orderModal.css";
import copy from "../../Assets/copy.svg";
import convertToIST from "../../services/istTime";
import api from "../../services/api";

function ConfirmationDinein({
  isOpen,
  onClose,
  refresh,
  onRejectClicked,
  order,
}) {
  const currency = "";
  const modalRef = useRef(null);
  const [time, setTime] = useState(30); // Default to 30 minutes
  const [staffName, setStaffName] = useState("");
  const [tableNumber, setTableNumber] = useState("");

  const handleStaffChange = (e) => {
    setStaffName(e.target.value);
  };

  const handleTableChange = (e) => {
    setTableNumber(e.target.value);
  };

  const incrementTime = () => {
    setTime((prevTime) => prevTime + 5); // Increment by 5 mins
  };

  const decrementTime = () => {
    if (time === 30) {
      return;
    }
    setTime((prevTime) => (prevTime > 0 ? prevTime - 5 : 0)); // Decrement by 5 mins but not below 0
  };

  const handleCopy = (orderId) => {
    navigator.clipboard.writeText(orderId.toString());
    alert(`Order ID #${orderId} copied to clipboard!`);
  };

  const handleAccept = () => {
    if (!staffName || !tableNumber) {
      alert("Please select a staff and table number to accept the order!");
      return;
    }
    api
      .patch(`/orders/Accepted/${order.id}`, {
        orderStatus: "Accepted",
        time,
        staffName,
        tableNumber,
      })
      .then(() => {
        refresh();
        handleOnClose();
      })
      .catch((error) => {
        console.log(error);
      });
  };

  const handleOnClose = () => {
    setStaffName("");
    setTableNumber("");
    setTime(30);
    onClose();
  };

  if (!isOpen) return null;

  return (
    <div className="modal-overlay-addDishes">
      <div className="modal-content-addDishes" ref={modalRef}>
        <div className="close" onClick={handleOnClose}>
          <span></span>
        </div>
        <div className="modal-confirm-header">
          <h6>ACCEPT ORDER?</h6>
        </div>
        <div className="content-details-dishes">
          <div className="orderIdDetails">
            <div className="orderId">
              <p>Order ID #{order.id}</p>
            </div>
            <div className="copyButton" onClick={() => handleCopy(order.id)}>
              <img src={copy} alt="copy" />
            </div>
          </div>
          <div className="customeDetails-orderType">
            <div className="CustomerDetails">
              <div className="OrderName">
                <p>{order.user.name}</p>
              </div>
              <div className="ordertimesdetails">
                <div className="time">
                  {convertToIST(order.createdTime).time}
                </div>
                <div className="seperation">|</div>
                <div className="orderdayDate">
                  {convertToIST(order.createdTime).date}
                </div>
              </div>
            </div>
            <div className="orderType">
              <p>{order.orderType.replace("Dine-in", "Dine In")}</p>
            </div>
          </div>
          <div className="itemDetails">
            {order.user.items.map((item) => (
              <div className="Single-Items">
                <p>{item.name}</p>
                <small>
                  {currency}
                  {item.price} | Qty: {item.quantity}
                </small>
              </div>
            ))}
            <form>
              <label>Assign Table</label>
              <select
                name="Tablenumber"
                className="tableNumber"
                value={tableNumber}
                onChange={handleTableChange}
              >
                <option value="" disabled>
                  Select Table
                </option>
                <option value="Table 1">Table 1</option>
                <option value="Table 2">Table 2</option>
                <option value="Table 3">Table 3</option>
                <option value="Table 4">Table 4</option>
              </select>

              <label>Assign Staff</label>
              <select
                name="StaffName"
                className="StaffName"
                value={staffName}
                onChange={handleStaffChange}
              >
                <option value="" disabled>
                  Select Staff
                </option>
                <option value="Staff 1">Staff 1</option>
                <option value="Staff 2">Staff 2</option>
                <option value="Staff 3">Staff 3</option>
                <option value="Staff 4">Staff 4</option>
              </select>
            </form>
            <div className="setTimeEstimation">
              <button onClick={decrementTime}>-</button>
              <div>{time} mins</div>
              <button onClick={incrementTime}>+</button>
            </div>
            <div className="Action-button">
              <button className="reject" onClick={onRejectClicked}>
                Reject
              </button>
              <button className="accept" onClick={handleAccept}>
                Accept
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default ConfirmationDinein;
