import React, { useState, useEffect } from "react";
import "../OrderTable.css";
import convertToIST from "../../../../services/istTime";
import ConfirmationDinein from "../../../components/ConfirmationDinein";
import RejectModal from "../../../components/RejectModal";

function OrdersTableDinein({ items, refresh }) {
  const [showData, setShowData] = useState([]);
  const [selectedOrder, setSelectedOrder] = useState(null);
  const [confirmModalOpen, setConfirmModalOpen] = useState(false);
  const [rejectModalOpen, setRejectModalOpen] = useState(false);

  const handleAccept = (item) => {
    setSelectedOrder(item);
    setConfirmModalOpen(true);
  };
  const handleReject = (item) => {
    setSelectedOrder(item);
    setRejectModalOpen(true);
  };
  const handleConfirmModalClose = () => {
    setConfirmModalOpen(false);
  };

  const handleRejectModalClose = () => {
    setRejectModalOpen(false);
  };

  useEffect(() => {
    setShowData(
      items.filter((order) => {
        return (
          order.user.orderStatus !== "Cancelled" &&
          order.user.orderStatus === "Order Placed"
        );
      })
    );
  }, [items]);
  return (
    <>
      <table className="orders-table">
        <thead>
          <tr>
            <th>Order ID</th>
            <th>Name</th>
            <th>Order Time</th>
            <th>Arival Time</th>
            <th>Items</th>
            <th>Total Amt.</th>
            <th>Payment</th>
            <th>Actions</th>
          </tr>
        </thead>
        <tbody>
          {showData.length === 0 ? (
            <tr>
              <td colSpan="8">No Request Available</td>
            </tr>
          ) : (
            showData.map((item) => (
              <tr key={item.id}>
                <td>
                  <p>{item.id}</p>
                </td>
                <td>{item.user.name}</td>
                <td>{convertToIST(item.updatedtime).time}</td>
                <td>{item.user.pickupTime}</td>
                <td>{item.user.items.length}</td>
                <td> {item.user.subTotal}</td>
                <td>paid</td>
                <td>
                  <button
                    className="mark-unavailable"
                    onClick={() => handleAccept(item)}
                  >
                    Accept
                  </button>

                  <button className="delete" onClick={() => handleReject(item)}>
                    Cancel
                  </button>
                  <button className="more-options"></button>
                </td>
              </tr>
            ))
          )}
        </tbody>
      </table>
      <ConfirmationDinein
        isOpen={confirmModalOpen}
        onClose={handleConfirmModalClose}
        order={selectedOrder}
        onRejectClicked={() => {
          setConfirmModalOpen(false);
          setRejectModalOpen(true);
        }}
        refresh={refresh}
      />
      <RejectModal
        isOpen={rejectModalOpen}
        order={selectedOrder}
        onClose={handleRejectModalClose}
        refresh={refresh}
      />
    </>
  );
}

export default OrdersTableDinein;
