import React from "react";
const categories = ["Take Away", "Dine in", "Completed", "Cancelled"];
function CatagoriesOrder({ activeCategory, onSelectCategory }) {
  return (
    <div className="category-filter">
      {categories.map((category, index) => (
        <button
          key={index}
          className={`category-button ${
            activeCategory === category ? "active" : ""
          }`}
          onClick={() => onSelectCategory(category)}
        >
          {category}
        </button>
      ))}
    </div>
  );
}

export default CatagoriesOrder;
