import moment from "moment-timezone";

function convertToIST(utcTimestamp) {
  const istTime = moment(utcTimestamp).tz("Asia/Kolkata");
  const formattedTime = istTime.format("hh:mm A");

  const today = moment().tz("Asia/Kolkata").startOf("day");
  const yesterday = moment()
    .tz("Asia/Kolkata")
    .subtract(1, "day")
    .startOf("day");

  let formattedDate;
  if (istTime.isSame(today, "day")) {
    formattedDate = "Today";
  } else if (istTime.isSame(yesterday, "day")) {
    formattedDate = "Yesterday";
  } else {
    formattedDate = istTime.format("DD-MM-YYYY"); // Format as actual date
  }

  return { time: formattedTime, date: formattedDate };
}

export default convertToIST;
