import React, { useEffect, useState } from "react";
import DashboardHeader from "./header/Dashboardheader";
import Status from "./status/Status";
import Orders from "./orders/order";
import filter from "../../Assets/filter.svg";
import api from "../../services/api";
import { replace, useNavigate } from "react-router-dom";
import ConfirmationModal from "../components/ConfirmationModal";
import ConfirmationDinein from "../components/ConfirmationDinein";
import RejectModal from "../components/RejectModal";
function DashboardContents() {
  const [orders, setOrders] = React.useState([]);
  const [refresh, setRefresh] = React.useState(false);
  const [searchQuery, setSearchQuery] = useState("");
  // const [openConfirmationModal, setOpenConfirmationModal] = useState(false);
  // const [openConfirmationDinein, setOpenConfirmationDinein] = useState(false);
  // const [openRejectModal, setOpenRejectModal] = useState(false);

  // const handleOpenConfirmationModal = () => {
  //   setOpenConfirmationModal(true);
  // };
  // const handleOpenConfirmationDinein = () => {
  //   setOpenConfirmationDinein(true);
  // };

  const navigate = useNavigate();
  const [resturantName, setResturantName] = useState("");
  useEffect(() => {
    // let eventSource;
    // const initEventSource = async () => {
    //   // Establish the connection to the server
    //   eventSource = new EventSource(`${url}/orders/takeaway/Joe's%20Pizza`);

    //   eventSource.onmessage = function (event) {
    //     try {
    //       const newOrder = JSON.parse(event.data);
    //       setOrders((prevOrders) => {
    //         const updatedOrders = [...prevOrders, newOrder];
    //         return updatedOrders;
    //       });
    //     } catch (error) {
    //       console.error("Error parsing JSON!", error);
    //     }
    //   };

    //   eventSource.onerror = function (error) {
    //     console.error("EventSource failed:", error);
    //     eventSource.close(); // Close the connection if there's an error
    //   };
    // };

    // initEventSource();

    // // Cleanup function to close the SSE connection when the component unmounts
    // return () => {
    //   if (eventSource) {
    //     eventSource.close();
    //   }
    // };
    const token = localStorage.getItem("token");
    if (!token) {
      navigate("/", { replace: true });
    }
    const TokenString = JSON.parse(token);
    const name = TokenString.restaurant;
    setResturantName(name);

    api
      .get(`/orders/takeaway/${name}`)
      .then((response) => {
        setOrders(response.data.orders);
      })
      .catch((error) => {
        console.log(error);
      });
    setRefresh(false);
  }, [refresh]);

  const handleRefresh = () => {
    setRefresh(true);
  };

  const handleSearchChange = (event) => {
    setSearchQuery(event.target.value);
  };

  const getReorderedOrders = () => {
    if (!searchQuery) {
      return orders; // Return all orders if no search query
    }

    // Separate matching and non-matching orders
    const matchingOrders = orders.filter((order) =>
      order.id.toLowerCase().includes(searchQuery.toLowerCase())
    );

    const nonMatchingOrders = orders.filter(
      (order) => !order.id.toLowerCase().includes(searchQuery.toLowerCase())
    );

    // Combine matching orders at the top
    return [...matchingOrders, ...nonMatchingOrders];
  };

  return (
    <div className="DashboardContent">
      <DashboardHeader
        searchQuery={searchQuery}
        handleSearchChange={handleSearchChange}
      />
      <Status name={resturantName} />
      <div className="headersOrderFilter">
        <div className="ordersfiltercontent">
          <h2 className="allorderHeader">All Orders</h2>
          <div className="orderFilter">
            <img src={filter} alt="filter" className="filterIcon" />
            <button className="orderFilterButton">Filters</button>
          </div>
        </div>
        <h2 className="InprogresscontentHeading">In Progress</h2>
      </div>
      <Orders order={getReorderedOrders()} refresh={handleRefresh} />
      {/* <ConfirmationModal isOpen={true} /> */}
      {/* <ConfirmationDinein isOpen={true} /> */}
      {/* <RejectModal isOpen={true} /> */}
    </div>
  );
}

export default DashboardContents;
